import { styled } from 'styled-components';

import * as device from '#theme/devices';

export const StyledProductTitle = styled.h2`
  margin: 0;
  ${({ theme }) => theme.textStyle.productTitle.mobile};

  @media ${device.tablet} {
    ${({ theme }) => theme.textStyle.productTitle.tablet};
  }

  @media ${device.laptop} {
    ${({ theme }) => theme.textStyle.productTitle.laptop};
  }
`;
